<template>
  <el-form 
    ref="settings" 
    :model="settings"
    label-width="240px"
    label-position="left"
    style="padding: 16px 48px 16px"
  >
    <div class="tip">
      <h3 >MND Assistant </h3>
      <el-divider></el-divider>
            
    </div>
    <el-form-item label="">
      <el-input v-model="intent.question" placeholder="Q)    ?">
      </el-input>
    </el-form-item>

    <el-form-item label="">
      <el-input v-model="intent.answer" placeholder="A)    000-0000" >
      </el-input>
    </el-form-item>
    <div>
      <el-form-item label="  ">
        <el-radio-group v-model="intent.dataType">
          <el-radio label="1" border></el-radio>
          <el-radio label="2" border></el-radio>
          <el-radio label="3" border></el-radio>
          <el-radio label="4" border></el-radio>
        </el-radio-group>
      </el-form-item>
    </div>
    <el-form-item label="   ">
      <el-switch v-model="settings.do_alert" />
    </el-form-item>
    <div style="text-align: center">
      <el-button
        type="primary"
        class="button-boing"
        round
        @click="onSubmit"
      >
        
      </el-button>

      <el-button
        type="notify"
        class="button-boing"
        round
      >
        
      </el-button>
    </div>
    

  </el-form>
  
</template>

<script>
export default {
  data(){
    return {
      intent: {
        question: '',
        answer: '',
        dataType: ''
      },
      settings: {
        user_name: '',
        do_alert: true
      }
    }
  },
  mounted() {
    if(localStorage.getItem('settings')) {
      try {
        this.settings = JSON.parse(localStorage.getItem('settings'));
      } catch(e) {
        localStorage.removeItem('settings');
      }
    }
  },
  methods: {
    onSubmit() {
      const loading = this.$loading({
        lock: true,
        text: '   ...',
        background: 'rgba(0, 0, 0, 0.7)'
      });


      let th = this
      setTimeout(()=>{

        // 
        let intent = this.intent;
        console.log(intent)
        th.$store.dispatch('teachai/setIntent', intent, {root: true});

        loading.close();
        this.$message({
          message: '!',
          type: 'success',
          showClose: true
        }

        
      )}, 5500);

      // const parsedSettings = JSON.stringify(this.settings);
      // localStorage.setItem('settings', parsedSettings);

    }
  }
}

</script>

<style>
  .a{border-radius : 30px;}
  .tip{
    line-height: 80%;
    padding: 3px 15px 20px;
    background-color: #ecf8ff;
    border-radius: 4px;
    border-left: 5px solid #50bfff;
    margin: 20px 0;
    text-align: left;
  }
</style>